header {
  position: sticky;
  top: 0;
  z-index: 100;
  width: 100%;
}

header ~ section:target:before {
  content: "";
  display: block;
  height: 20px;
  margin: -20px 0 0;
}

header .hiring a {
  position: fixed;
  border-radius: 0.5rem;
  top: 80px;
  right: 20px;
  text-decoration: none;
  border: 2px solid #409AD6;
  padding: 1rem 2rem;
  background: linear-gradient(60deg, #3BCCE5 0%, #409AD6 100%);
  color: #FFF;
  display: flex;
  align-items: center;
}

header .hiring a img {
  width: 28px;
  margin-right: 8px;
}

header nav {
  background-color: #000;
  display: flex;
}

header nav a {
  color: #FFF;
  display: block;
  border-left: 1px solid #313131;
  border-right: 1px solid #313131;
  padding: 1.5rem 0;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
  flex: 1;
  text-align: center;
  letter-spacing: 0.2rem;
  font-size: 0.8rem;
  border-bottom: 2px solid #313131;
  max-height: 60px;
}

header nav a.brand {
  flex: 0.8;
}

header nav a.brand,
header nav a.brand:hover {
  background: none;
  border-bottom: 2px solid #313131;
}

header nav a.brand img,
header nav a.brand:hover img {
  display: inherit;
  text-align: center;
  max-width: 90%;
  margin: -0.25rem auto;
  height: 22px;
  padding: 0.2rem;
}

header nav a.blog {
  flex: 0.5;
}

header nav a.blog.active {
  background: linear-gradient(60deg, #3BCCE5 0%, #409AD6 100%);
  border-left: none;
  border-bottom: none;
}

header nav a:last-child {
  border-right: none;
}

header nav a:first-child {
  border-left: none;
}

header nav a:hover {
  background: linear-gradient(60deg, #3BCCE5 0%, #409AD6 100%);
  color: #FFF;
  border-bottom: none;
}

header nav a.blog:hover {
  background: #FFF;
}

@supports (mix-blend-mode: lighten) {
  header nav a.blog:hover span {
    overflow: hidden;
    border: 1px solid transparent;
    display: inline-block;
    position: relative;
    color: #000;
    background: #fff;
    mix-blend-mode: multiply;
  }

  header nav a.blog:hover span::before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: linear-gradient(60deg, #3BCCE5 0%, #409AD6 100%);
    pointer-events: none;
  }

  header nav a.blog:hover span::before {
    mix-blend-mode: screen;
  }
}

#company {
  background-size: cover;
  background-image: url("../img/header_large.jpg");
  background-position: center center;
  background-color: #959595;
  background-attachment: fixed;
  padding-bottom: 5rem;
  padding-top: 5rem;
}

@media screen and (max-width: 768px) {
  #company {
    background-image: url("../img/header_small.jpg");
    background-attachment: inherit;
  }
}

#company .logo {
  display: inherit;
  padding: 2rem;
  max-width: 100%;
  margin: 0 auto;
  text-align: center;
}

#company .introduction {
  color: #FFF;
  margin: auto;
  width: 960px;
  max-width: 100%;
  display: flex;
  padding-left: 30px;
  padding-right: 30px;
  line-height: 1.6rem;
}

#company .introduction h2::after {
  content: ' ';
  display: block;
  padding-top: 0.8rem;
  margin-left: -0.6rem;
  border-bottom: 1px solid #FFF;
  width: 50px;
}

#company .introduction div:first-child {
  flex: 1;
}

#company .introduction div:first-child p {
  font-size: 1.3rem;
  line-height: 2rem;
}

#company .introduction div:last-child {
  margin-left: 5rem;
  flex: 2;
}

#company .introduction .gradient {
  font-size: 1.2rem;
  font-weight: bold;
}

@supports (mix-blend-mode: lighten) {
  #company .introduction .gradient {
    overflow: hidden;
    border: 1px solid transparent;
    display: inline-block;
    position: relative;
    color: #fff;
    background: #000;
    mix-blend-mode: lighten;
  }

  #company .introduction .gradient::before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: linear-gradient(60deg, #3BCCE5 0%, #409AD6 100%);
    pointer-events: none;
  }

  #company .introduction .gradient::before {
    mix-blend-mode: multiply;
  }
}

#company .introduction a {
  text-transform: uppercase;
  text-decoration: none;
  color: #FFF;
  letter-spacing: 0.2rem;
  font-size: 0.8rem;
  padding-bottom: 0.1rem;
  border-bottom: 1px dotted #FFF;
}

#company .introduction a:hover {
  color: #3BCCE5;
  border-color: #3BCCE5;
}

@media screen and (max-width: 1024px) {
  header nav a {
    display: none;
  }

  header nav a.brand,
  header nav a.blog {
    display: initial;
  }

  header nav a.brand,
  header nav a.brand:hover {
    background-size: 75%;
  }

  #company .introduction {
    flex-direction: column;
  }

  #company .introduction div:last-child {
    margin-left: 0;
  }
}

section#numbers {
