  font-size: 5rem;
  font-weight: bold;
  padding-bottom: 1rem;
}

@supports (mix-blend-mode: lighten) {
  section#numbers ul li div:first-child {
    overflow: hidden;
    border: 1px solid transparent;
    display: inline-block;
    position: relative;
    color: #000;
    background: #fff;
    mix-blend-mode: multiply;
  }

  section#numbers ul li div:first-child::before {
    content: '';
