section#skills {
  background: linear-gradient(60deg, #3BCCE5 0%, #409AD6 100%);
  color: #FFF;
}

section#skills .wrapper {
  margin: auto;
  width: 960px;
  max-width: 100%;
  padding: 30px;
}

section#skills .wrapper ul li {
  margin: 1rem;
}

@media screen and (max-width: 1024px) {
  section#skills .wrapper ul li {
    min-width: calc(50% - 2rem);
    max-width: calc(50% - 2rem);
  }
}

@media screen and (max-width: 512px) {
