  width: 30px;
}

section#contact {
  background: linear-gradient(60deg, #3BCCE5 0%, #409AD6 100%);
  color: #FFF;
  padding-bottom: 5rem;
}

section#contact > div {
  display: flex;
}

section#contact > div > div {
