  color: #000;
}

section#contact > div > div:first-child {
  max-height: 550px;
}

section#contact > div > div:last-child {
  padding: 5rem;
}

section#contact > div > div:last-child h2::after {
  content: ' ';
  display: block;
  padding-top: 0.8rem;
  margin-left: -0.6rem;
  border-bottom: 1px solid #000;
  width: 50px;
}

section#contact > div > div:last-child p {
  line-height: 1.5rem;
}

section#contact > div > div:last-child strong {
  font-weight: bold;
}

section#contact > div > div:last-child a {
  color: #000;
}

section#contact > div > div:last-child a:hover {
  color: #3BCCE5;
}

section#contact > div > div:last-child a.button {
  display: inline-block;
  width: auto;
  background: linear-gradient(60deg, #3BCCE5 0%, #409AD6 100%);
  color: #FFF;
  padding: 0.5rem 1rem;
  border-radius: 0.3rem;
  text-decoration: none;
  font-weight: bold;
}

section#contact ul {
  justify-content: center;
}

@media screen and (min-width: 768px) {
  section#contact ul {
    width: 60%;
  }
}

section#contact ul li a {
  position: relative;
  width: 5rem;
  height: 5rem;
  border: 2px solid white;
  border-radius: 50%;
  display: inline-block;
}

@media screen and (max-width: 520px) {
  section#contact ul li a {
    width: 3rem;
    height: 3rem;
  }

  section#contact ul li a .fa-3x {
    font-size: 1.5em;
  }
}

section#contact ul li a:hover {
  background-color: #FFF;
}

section#contact ul li a:hover i {
  color: #409AD6;
}

section#contact ul li a i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (max-width: 768px) {
  section#contact {
    letter-spacing: initial;
  }

  section#contact > div {
    flex-direction: column;
  }

  section#contact > div > div:first-child img {
    height: auto;
  }

  section#contact > div > div:last-child {
    padding: 2rem;
  }
}

footer {
  background: #000;
}

footer .wrapper {
  margin: auto;
  padding: 5rem 30px 2rem;
  color: #959595;
  font-size: 0.7rem;
}

footer .wrapper img {
  display: block;
  height: 150px;
  max-width: 100%;
  margin: 5rem auto;
}

footer .wrapper img.arrow {
  height: 70px;
  margin-top: 0;
}

footer .wrapper span:first-child {
  float: left;
}

footer .wrapper span:last-child {
  float: right;
}

footer .wrapper a {
  text-decoration: underline;
  color: inherit;
}

footer .wrapper a:hover {
  text-decoration: underline;
