section#portfolio {
  color: #FFF;
  background: #000;
}

section#portfolio .wrapper {
  display: flex;
  flex-wrap: wrap;
}

section#portfolio .wrapper h1 {
  flex: 1;
  text-align: center;
}

section#portfolio .wrapper > div {
  flex: 1;
}

section#portfolio .wrapper > div a {
  font-size: 0.7rem;
}

section#portfolio .wrapper > div img {
  max-width: 100%;
  min-width: 220px;
  min-height: 330px;
  object-fit: cover;
}

section#portfolio .wrapper > div > div {
  padding: 1rem 3rem;
}

section#portfolio .wrapper > div > div h2::after {
  content: ' ';
  display: block;
  padding-top: 0.8rem;
  margin-left: -0.6rem;
  border-bottom: 1px solid #FFF;
  width: 50px;
}

section#portfolio .wrapper > div > div h2 a {
  font-size: inherit;
}

section#portfolio .wrapper > div > div p {
  font-size: 0.7rem;
  color: #959595;
  line-height: 1.3rem;
}

section#portfolio .wrapper > div > div a.read-more {
  display: inline-block;
  width: 40%;
  transition: width 1s ease-in-out;
}

section#portfolio .wrapper > div > div a.read-more .arrow_box {
  margin-left: -0.6rem;
}

section#portfolio .wrapper > div > div a.read-more .arrow_box {
  transition: inherit;
  margin-top: 0.75rem;
  position: relative;
  background: #FFF;
  border: 1px solid #FFF;
  width: 100%;
}

section#portfolio .wrapper > div > div a.read-more .arrow_box:after,
section#portfolio .wrapper > div > div a.read-more .arrow_box:before {
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

section#portfolio .wrapper > div > div a.read-more .arrow_box:after {
  border-color: rgba(0, 0, 0, 0);
  border-width: 4px;
  margin-top: -4px;
}

section#portfolio .wrapper > div > div a.read-more .arrow_box:before {
  border-color: rgba(0, 0, 0, 0);
  border-width: 5px;
  margin-top: -5px;
}

section#portfolio .wrapper > div > div a.read-more .arrow_box_right:after,
section#portfolio .wrapper > div > div a.read-more .arrow_box_right:before {
  left: 100%;
  top: 50%;
}

section#portfolio .wrapper > div > div a.read-more .arrow_box_right:after {
  border-left-color: #FFF;
}

section#portfolio .wrapper > div > div a.read-more .arrow_box_right:before {
  border-left-color: #FFF;
}

section#portfolio .wrapper > div > div a.read-more .arrow_box_left:after,
section#portfolio .wrapper > div > div a.read-more .arrow_box_left:before {
  right: 100%;
  top: 50%;
}

section#portfolio .wrapper > div > div a.read-more .arrow_box_left:after {
  border-right-color: #FFF;
}

section#portfolio .wrapper > div > div a.read-more .arrow_box_left:before {
  border-right-color: #FFF;
}

section#portfolio .wrapper > div > div a.read-more:hover {
  width: 100%;
}

section#portfolio .wrapper > div > div a.read-more:hover .arrow_box {
  transition: inherit;
  margin-top: 0.75rem;
  position: relative;
  background: #3BCCE5;
  border: 1px solid #3BCCE5;
  width: 100%;
}

section#portfolio .wrapper > div > div a.read-more:hover .arrow_box:after,
section#portfolio .wrapper > div > div a.read-more:hover .arrow_box:before {
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

section#portfolio .wrapper > div > div a.read-more:hover .arrow_box:after {
  border-color: rgba(0, 0, 0, 0);
  border-width: 4px;
  margin-top: -4px;
}

section#portfolio .wrapper > div > div a.read-more:hover .arrow_box:before {
  border-color: rgba(0, 0, 0, 0);
  border-width: 5px;
  margin-top: -5px;
}

section#portfolio .wrapper > div > div a.read-more:hover .arrow_box_right:after,
section#portfolio .wrapper > div > div a.read-more:hover .arrow_box_right:before {
  left: 100%;
  top: 50%;
}

section#portfolio .wrapper > div > div a.read-more:hover .arrow_box_right:after {
  border-left-color: #3BCCE5;
}

section#portfolio .wrapper > div > div a.read-more:hover .arrow_box_right:before {
  border-left-color: #3BCCE5;
}

section#portfolio .wrapper > div > div a.read-more:hover .arrow_box_left:after,
section#portfolio .wrapper > div > div a.read-more:hover .arrow_box_left:before {
  right: 100%;
  top: 50%;
}

section#portfolio .wrapper > div > div a.read-more:hover .arrow_box_left:after {
  border-right-color: #3BCCE5;
}

section#portfolio .wrapper > div > div a.read-more:hover .arrow_box_left:before {
  border-right-color: #3BCCE5;
}

section#work {
  background-color: #000;
  color: #EFEFEF;
}

section#work .wrapper {
  margin: auto;
  display: flex;
  flex-wrap: wrap;
}

section#work .wrapper h1 {
  flex: 1;
  color: #FFF;
}

section#work .wrapper article > img {
  width: 100%;
}

section#work .wrapper article .wrapper-content {
  margin-left: 1rem;
  margin-right: 1rem;
}

section#work .wrapper article .wrapper-content > h2::after,
section#work .wrapper article .wrapper-content .content::after {
  content: ' ';
  display: block;
  padding-top: 0.8rem;
  margin-left: -0.6rem;
  border-bottom: 1px solid #EFEFEF;
  width: 50px;
}

section#work .wrapper .content-footer {
  padding: 1rem 0;
}

section#work .wrapper .share {
  border-color: #313131;
}

section#work .wrapper .share a {
  color: #EFEFEF;
}

section#work ul.skill li,
section#portfolio ul.skill li {
  border: 2px solid #313131;
  border-radius: 0.5rem;
  width: auto;
  position: relative;
  display: inline-block;
  padding: 0.5rem 0.5rem 0.5rem 30px;
  font-size: 0.7rem;
  color: #969696;
}

section#work ul.skill li span::before,
section#portfolio ul.skill li span::before {
  content: '';
  display: block;
  position: absolute;
  top: 5px;
  left: 8px;
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
}

section#work ul.skill li:hover,
section#portfolio ul.skill li:hover {
  background: linear-gradient(60deg, #3BCCE5 0%, #409AD6 100%);
  border-color: transparent;
  color: #FFF;
}

section#work ul.skill li.android span::before,
section#portfolio ul.skill li.android span::before {
  content: "";
}

section#work ul.skill li.ios span::before,
section#portfolio ul.skill li.ios span::before {
  content: "";
}

section#work ul.skill li.web span::before,
section#portfolio ul.skill li.web span::before {
  content: "";
}

@media screen and (min-width: 768px) {
  section#portfolio .wrapper > div {
    min-width: 50%;
    max-width: 50%;
  }
}

@media screen and (max-width: 768px) {
  section#portfolio .wrapper > div {
    min-width: 100%;
    max-width: 100%;
  }
}

@media screen and (min-width: 1200px) {
  section#work .wrapper {
    width: 1200px;
  }

  section#work .content {
    margin: inherit;
  }

  section#portfolio .wrapper > div {
    border: 1px solid #313131;
    border-left: none;
    min-width: 25%;
    max-width: 25%;
  }

  section#portfolio .wrapper > div img {
    max-width: 100%;
  }
}

