@import '../../node_modules/lightbox2/dist/css/lightbox.css';
@import 'variables';
@import 'mixins';
@import 'header';
@import 'numbers';
@import 'skills';
@import 'contact';
@import 'footer';
@import 'portfolio';
@import 'blog';
@import 'casting';
